import "./styles.css";
import "./neon.css";

const firstStage = `
<div id="app">
      <div class="center-text">
        <div>
          <span class="text-typing" id="hi">Привет</span>
          <span class="display-none" id="ania">Аня, просто хотел поздравить тебя с</span>
          <span class="display-none" id="text-cake">Вот тортик</span>
          <img
            src="https://www.pngarts.com/files/3/Cake-PNG-Image-Transparent.png"
            id="cake"
          />
          <img
            src="https://pngriver.com/wp-content/uploads/2018/04/Download-Penguin-PNG-Images.png"
            id="pinguin"
          />
        </div>
      </div>
    </div>
`;

const secondStage = `
<div class="sign">
  <span class="fast-flicker">пра</span>зднич<span class="flicker">к</span>OM
</div>
`;

const animationClass = "text-typing";
const time4sAnimationClass = "text-typing_4sec";

const root = document.getElementById("root");

root.innerHTML = firstStage;

const hi = document.getElementById("hi");
const ania = document.getElementById("ania");
const zanaves = document.getElementById("zanaves");
// const party = document.getElementById("party");
// const textCake = document.getElementById("text-cake");
// const cakeImg = document.getElementById("cake");
// const pinguinImg = document.getElementById("pinguin");

hi.onanimationend = () => {
  hi.classList = "display-none";
  ania.className = animationClass + " " + time4sAnimationClass;

  setTimeout(() => (zanaves.className += " zanaves_open"), 1000);
  ania.onanimationend = () => {
    ania.className = "display-none";
    zanaves.className += " zanaves_up";
    root.innerHTML = secondStage;
    document.body.className = "neon";
  };
};
